@import "~@material/typography/dist/mdc.typography.css";
@import "~@material/dialog/dist/mdc.dialog.css";

main {
    transition: all 2s linear;

    & .mdc-dialog__container {
        height: 90%;
        width: 100%;
        & > .mdc-dialog__surface {
            background-image: linear-gradient(
                    rgba(196, 212, 236, 0.45),
                    rgba(196, 212, 236, 0.45)
                ),
                url(./assets/jpg/splash-background.jpg);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
            height: 40%;
            width: 60%;
        }
    }
}

footer {
    font-size: 0.8em;
    margin: 1em auto;
    max-width: 60%;
    .copyright {
        display: block;
        text-align: center;
    }
}

.collapsed {
    display: none;
}

.hidden {
    opacity: 0;
}

/* https://loading.io/css/ */
.lds-ripple {
    display: block;
    margin: 0 auto;
    position: relative;
    width: 64px;
    height: 64px;
}
.lds-ripple div {
    position: absolute;
    border: 4px solid #3bff6c;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes lds-ripple {
    0% {
        top: 28px;
        left: 28px;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        top: -1px;
        left: -1px;
        width: 58px;
        height: 58px;
        opacity: 0;
    }
}
